table{
  border-radius:4px;
  font-family: sans-serif, Calibri, Gill Sans, Gill Sans MT,  Trebuchet MS;
  background-color: white;
  table-layout: fixed;
}
table, th, td {
  border-collapse: separate; 
  border-spacing: 0;
  font-size: 12px;
}


table th{
font-weight: 600;
height:22px;
background-color: #F4F4F4;
font-size:12px ;
font-weight: bold;
}

th ,td{
 /* width:fit-content; */
  width:65px;
  min-height:30px;
  padding: 5px;
  overflow-wrap: break-word;
}

thead{
  position: sticky;
  top:0;
  z-index:2;
}

thead tr:first-child th{
  border-top:2px solid #dcdada
}

td{
  min-width:40px;
  border-bottom: 2px solid #F4F4F4;
}

.input-field{
width:85%;
height:70%;
border:none;
text-align: center;
outline:none;
border-radius:7px;
font-size: 12px;
cursor: pointer;
}

.input-field:focus,

.input-field:active{
border:1px solid #dcdada;
background-color: #F4F4F4;
}

td.static{
  position:sticky;
  left:0;
  background-color:white;
  z-index:1
}

th.static{
  background-color: #F4F4F4;
  position:sticky;
  z-index:1
}

 td:nth-child(1) ,th:nth-child(1){
  width:50px;
  min-width:50px;
  left:0;
} 

td:nth-child(2) ,th:nth-child(2){
  width:130px;
  max-width:130px;
  left:60px;
  font-weight:bold;
}

td:nth-child(3) ,th:nth-child(3){
  width:130px;
  max-width:130px;
  left:200px;
  /* border-right:1px solid #dcdada; */
}

td:nth-child(4) ,th:nth-child(4){
  width:30;
  max-width:30px;
  left:340px;
  border-right:1px solid #dcdada;
}

.border{
  border-top: 1px solid #dcdada;
}

.ant-tooltip-inner {
  color: #CD0720 !important;
  background-color: white !important; 
  font-weight:600 !important;
}

.ant-tooltip-placement-bottom .ant-tooltip-arrow, .ant-tooltip-placement-bottomLeft .ant-tooltip-arrow, .ant-tooltip-placement-bottomRight .ant-tooltip-arrow {
   border-bottom-color: white !important; 
}